const state = {
  isShowNotification: false,
  notificationMsg: ''
}

const getters = {
}

const mutations = {
  SHOW_NOTIFICATION (state, msg) {
    state.isShowNotification = true
    state.notificationMsg = msg
  },
  CLOSE_NOTIFICATION (state) {
    state.isShowNotification = false
    state.notificationMsg = ''
  }
}
const actions = {}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
